import Pokemon from "./Pokemon";
import { Wrapper } from "./PokemonList.styles";

const PokemonList = (props) => {
  return (
    <Wrapper>
      {props.pokemons.map((pokemon) => {
        return (
          <Pokemon
            key={pokemon.id}
            name={pokemon.name}
            img={pokemon.img}
            type={pokemon.type}
          />
        );
      })}
    </Wrapper>
  );
};

export default PokemonList;
