import { PokemonImage, PokemonName, PokemonWrapper } from "./Pokemon.styles";
import Type from "./Type";

const Pokemon = (props) => {
  return (
    <PokemonWrapper>
      <PokemonImage src={props.img} alt="pokemon" />
      {/* {props.type.map((type) => {
        return <Type key={type} type={type} />;
      })} */}
      <PokemonName>{props.name}</PokemonName>
    </PokemonWrapper>
  );
};

export default Pokemon;
