import PokemonList from "./components/PokemonList";
import SearchForm from "./components/SearchForm";
import pokemons from "./pokedex.json";
import "./App.css";

function App() {
  const firstFifty = pokemons.pokemon.slice(0, 50);

  return (
    <div className="App">
      <SearchForm />
      <PokemonList pokemons={firstFifty} />
    </div>
  );
}

export default App;
